import * as React from 'react';
import { Typography } from '@material-tailwind/react';
import { clsx } from 'clsx';
import PaginationTableScreening from '../pagination-table-screener';
import { CgSpinner } from 'react-icons/cg';
import { GoInbox } from 'react-icons/go';
import { useFieldContext } from '../../context/fieldsContext';

export default function TableScreening({
  dataTable,
  getScreeningForm,
  getScreeningDetailDocument,
  getDataDetailApplicant,
  resetState,
  activeApplicantId,
  isLoadingDataTable,
  setIsLoadingDataTable,
  getDataVerDocList,
}: any) {
  const TABLE_HEAD = ['Name & Profile'];
  const { state, dispatch } = useFieldContext();

  const onClick = (applicant_id: string, verdoc_id: string) => {
    resetState();
    getScreeningForm(applicant_id);
    getScreeningDetailDocument(verdoc_id);
    getDataDetailApplicant(applicant_id);
  };

  return (
    <React.Fragment>
      <div className="my-2">
        <table className="w-full text-left border-separate border-spacing-0 rounded-lg border overflow-hidden">
          <thead>
            <tr>
              {dataTable && dataTable.data.length > 0 && <th key="list_no" className="border-b border-blue-gray-100 bg-blue-gray-50 px-2">
                <Typography color="blue-gray" className="leading-none opacity-70 text-xs font-semibold p-2">
                  No
                </Typography>
              </th>}
              {TABLE_HEAD.map((head) => (
                <th key={head} className="border-b border-blue-gray-100 bg-blue-gray-50 p-2">
                  <Typography color="blue-gray" className="leading-none opacity-70 text-xs font-semibold">
                    {head}
                  </Typography>
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {isLoadingDataTable ? (
              <tr>
                <td>
                  <div className="p-2 flex items-center justify-center">
                    <CgSpinner className="w-5 h-5 animate-spin" />
                  </div>
                </td>
                <td>
                  <div className="p-2 flex items-center justify-center">
                    <CgSpinner className="w-5 h-5 animate-spin" />
                  </div>
                </td>
              </tr>
            ) : (
              <React.Fragment>
                {dataTable && dataTable.data.length > 0 && (
                  <React.Fragment>
                    {dataTable?.data.map((item, index) => {
                      return (
                        <tr key={index} onClick={() => onClick(item.applicant_id, item.verdoc_id)} className="hover:bg-gray-50 cursor-pointer">
                          <td
                            className={clsx(
                              'border-b-gray-100 border-b-2',
                              activeApplicantId === item.verdoc_id ? 'text-primary-500 bg-primary-50 border-b-2' : ''
                            )}
                          >
                            <Typography color="blue-gray" className={clsx("flex leading-none opacity-70 text-xs font-semibold items-center justify-center", activeApplicantId === item.verdoc_id ? 'text-primary-500' : '')}>
                              {index + 1 + (state.size * (state.page - 1))}
                            </Typography>
                          </td>
                          <td
                            className={clsx(
                              'p-2 border-b-gray-100 border-b-2',
                              activeApplicantId === item.verdoc_id ? 'text-primary-500 border-r-primary-500 border-r-4 bg-primary-50 border-b-2' : ''
                            )}
                          >
                            <p className={clsx("text-sm font-bold truncate max-w-full", activeApplicantId === item.verdoc_id ? 'text-primary-500' : '')}>
                              {item.fullname}
                            </p>
                            <Typography className={clsx("font-normal text-xs text-gray-600", activeApplicantId === item.verdoc_id ? 'text-primary-500' : '')}>
                              {item.email}
                            </Typography>
                            <Typography className={clsx("font-normal mt-2 text-xs", activeApplicantId === item.verdoc_id ? 'text-primary-500' : '')}>
                              {item.identity_no}
                            </Typography>
                          </td>
                        </tr>
                      );
                    })}
                  </React.Fragment>
                )}

                {dataTable && dataTable.data.length === 0 && (
                  <tr>
                    <td>
                      <div className="p-2 flex flex-col items-center justify-center space-y-2">
                        <GoInbox className="w-8 h-8" />
                        <p className="text-xs font-normal ">Data tidak ditemukan</p>
                      </div>
                    </td>
                  </tr>
                )}
              </React.Fragment>
            )}
          </tbody>
        </table>
      </div>
      {dataTable && dataTable.data.length > 0 && <PaginationTableScreening dataTable={dataTable} getDataVerDocList={getDataVerDocList} />}
    </React.Fragment>
  );
}
