import * as React from 'react';
import usePagination from '../../hooks/usePagination';
import { clsx } from 'clsx';
import { LuChevronLeft, LuChevronsLeft, LuChevronsRight, LuChevronRight } from 'react-icons/lu';
import { useFieldContext } from '../../context/fieldsContext';

const MemoPaginationTableScreening = ({ dataTable, getDataVerDocList }) => {
  const { state, dispatch } = useFieldContext();
  const { currentPage, handleChangePage, handleChangeRowsPerPage, paginateData, renderPagination, rowsPerPage, totalPage } = usePagination(
    dataTable,
    getDataVerDocList
  );

  const handleClickAction = async (page?: number) => {
    const payload = {
      ...state,
      page,
    };
    await getDataVerDocList(payload);
  };

  return (
    <div className="mt-2 font-normal -mr-6">
      <div className="flex items-center space-x-2">
        <label htmlFor="select_per_page">
          <p className="text-xs text-slate-700">Rows per page</p>
        </label>
        <select
          name="size"
          id="select_per_page"
          className="rounded-lg border p-1.5 ml-2 text-xs"
          onChange={handleChangeRowsPerPage}
          value={state.size}
        >
          <option value="10">10</option>
          <option value="30">30</option>
          <option value="50">50</option>
        </select>
        <p className="text-xs">
          Showing {paginateData()?.length} of {rowsPerPage}.
        </p>
      </div>

      {/* Tampilkan pagination */}
      <div className="mt-4 space-x-1 flex items-center overflow-y-auto pr-6">
        <button
          disabled={currentPage === 1}
          onClick={() => handleClickAction(1)}
          className="px-1 py-2 rounded-lg text-xs text-slate-900 hover:bg-gray-200"
        >
          <LuChevronsLeft className="w-4 h-5" />
        </button>
        <button
          disabled={currentPage - 1 <= 0}
          onClick={() => handleClickAction(currentPage - 1)}
          className="px-1 py-2 rounded-lg text-xs text-slate-900 hover:bg-gray-200"
        >
          <LuChevronLeft className="w-4 h-5" />
        </button>
        {renderPagination().map((page, index) => (
          <button
            disabled={currentPage === page}
            key={index}
            className={clsx(
              'px-3.5 py-2 rounded-lg text-xs text-slate-900 hover:bg-gray-200 disabled:hover:bg-gray-100',
              currentPage === page ? 'bg-gray-100' : 'bg-none',
              page === '...' && 'hover:bg-white'
            )}
            onClick={() => {
              if (typeof page === 'number') {
                handleChangePage(page);
              }
            }}
          >
            {page}
          </button>
        ))}
        <button
          disabled={currentPage === totalPage.length}
          onClick={() => handleClickAction(currentPage + 1)}
          className="px-1 py-2 rounded-lg text-xs text-slate-900 hover:bg-gray-200"
        >
          <LuChevronRight className="w-4 h-5" />
        </button>
        <button
          disabled={currentPage === totalPage.length}
          onClick={() => handleClickAction(totalPage.length)}
          className="px-1 py-2 rounded-lg text-xs text-slate-900 hover:bg-gray-200"
        >
          <LuChevronsRight className="w-4 h-5" />
        </button>
      </div>
    </div>
  );
};

const PaginationTableScreening = React.memo(MemoPaginationTableScreening);

export default PaginationTableScreening;
