import * as React from 'react';
import { Route, Routes } from 'react-router-dom';
import VerificationDocument from './components/verification-document';
import PreviewFileNewTab from './components/preview-file/PreviewFileNewTab';

export default function Router() {
  return (
    <Routes>
      <Route path="/" element={<VerificationDocument />} />
      <Route path="/screening/document/preview-file" element={<PreviewFileNewTab />} />
    </Routes>
  );
}
