import * as React from 'react';
import { clsx } from 'clsx';
import { useValidationContext } from '../../../context/validationContext';
import { changeValueScreeningDocument } from '../../../context/action/validationAction';

const MemoizeValidationComponentValue = ({ validationValue, validationData }) => {
  const { state, dispatch } = useValidationContext();
  return (
    <React.Fragment>
      <div key={validationValue.id} className="border-b last:border-b-0 first:border-t">
        {state && (
          <div
            className={clsx(
              'rounded-md m-2 text-sm space-y-1.5 p-2 ',
              state[`scr_${validationData.doc_type}_${validationValue.field_code}_status`] === 'lolos' ? 'bg-[#E8FFE2]' : state[`scr_${validationData.doc_type}_${validationValue.field_code}_status`] === 'gugur' ? 'bg-[#FFE2E2]' : 'bg-orange-50'
            )}
            // className={clsx(
            //   'rounded-md m-2 text-sm space-y-1.5 p-2 ',
            //   state[`scr_${validationData.doc_type}_${validationValue.field_code}_status`] === 'lolos' ? 'bg-[#E8FFE2]' : 'bg-[#FFE2E2]'
            // )}
          >
            <h4 className="font-bold">
              {validationValue.field_name} <span className="text-red-500 text-xs">*</span>
            </h4>
            <p className="text-gray-500 font-light">{`(Pastikan ${validationValue.field_name} sesuai)`}</p>
            <hr className="border-[0.5px] border-gray-500" />
            {state['type_engine'] === 'ocr' && (
              <div className="text-gray-500">
                <h5>
                  <b>OCR Value:</b> <span className="font-light">{state[`ocr_${validationData.doc_type}_${validationValue.field_code}_value`]}</span>
                </h5>
              </div>
            )}
            {/* {state[`ocr_${validationData.doc_type}_${validationValue.field_code}_notes`] && ( */}
            <div className="text-gray-500">
              <h5>
                <b>Alasan:</b> <span className="font-light"> {state[`ocr_${validationData.doc_type}_${validationValue.field_code}_notes`]}</span>
              </h5>
            </div>
            {/* // )} */}

            <div className="space-x-1.5 w-full flex items-center">
              <input
                type="radio"
                id={`scr_${validationData.doc_type}_${validationValue.field_code}_sesuai`}
                value="lolos"
                name={`scr_${validationData.doc_type}_${validationValue.field_code}_status`}
                onChange={changeValueScreeningDocument(dispatch)}
                checked={state[`scr_${validationData.doc_type}_${validationValue.field_code}_status`] === 'lolos'}
              />
              <label htmlFor={`scr_${validationData.doc_type}_${validationValue.field_code}_sesuai`}>
                <span>Sesuai</span>
              </label>
              <input
                type="radio"
                id={`scr_${validationData.doc_type}_${validationValue.field_code}_tidak_sesuai`}
                value="gugur"
                name={`scr_${validationData.doc_type}_${validationValue.field_code}_status`}
                onChange={changeValueScreeningDocument(dispatch)}
                checked={state[`scr_${validationData.doc_type}_${validationValue.field_code}_status`] === 'gugur'}
              />
              <label htmlFor={`scr_${validationData.doc_type}_${validationValue.field_code}_tidak_sesuai`}>
                <span>Tidak Sesuai</span>
              </label>
            </div>
          </div>
        )}
      </div>
    </React.Fragment>
  );
};

const ValidationComponentValue = React.memo(MemoizeValidationComponentValue);
export default ValidationComponentValue;
