import * as React from 'react';
import api from '../../../../../../utils/axios';
import Header from '../header-dashboard';
import ScreeningTabs from '../tabbing-screener';
import ValidationCard from '../validation-card';
import LastValidationComponentValue from '../validation-card/last-validation-component-value';
import ValidationComponentValue from '../validation-card/validation-component-value';
import DetailApplicant from '../detail-applicant';
import FooterScreeningDocument from '../footer-verification-document';
import { useFieldContext } from '../../context/fieldsContext';
import { RxMagnifyingGlass, RxCross2 } from 'react-icons/rx';
import { useValidationContext } from '../../context/validationContext';
import { useOptionsContext } from '../../context/optionsContext';
import { initialValueField } from '../../context/action/fieldsAction';
import { GoInbox } from 'react-icons/go';
import sessions from '../../../../../../utils/sessions';

export default function VerificationDocument() {
  const { state, dispatch } = useFieldContext();
  const { state: stateValidation, dispatch: dispatchValidation } = useValidationContext();
  const { options, setOptions } = useOptionsContext();

  const userLevel = JSON.parse(sessions.getSessionUser())?.userlevel === 'C6E897BE4C224CABBAE0AEB0E876E697';

  const [valueOptions, setValueOptions] = React.useState([]);

  const [dataVerDocList, setDataVerDocList] = React.useState<any>(null);
  const [isLoadingDataVerDocList, setIsLoadingDataVerDocList] = React.useState<boolean>(true);
  const [dataFormVerDoc, setDataFormVerDoc] = React.useState<any>([]);
  const [isLoadingDataFormVerDoc, setIsLoadingDataFormVerDoc] = React.useState<boolean>(true);

  const [detailValueDocument, setDetailValueDocument] = React.useState<any>([]);
  const [detailApplicant, setDetailApplicant] = React.useState<any>(null);
  const [isLoadingDetailApplicant, setIsLoadingDetailApplicant] = React.useState<boolean>(true);

  const [activeVerdocId, setActiveVerdocId] = React.useState<string>('');
  const [listDashboardMini, setListDashboardMini] = React.useState([]);
  const [isLoadingDashboardMini, setIsLoadingDashboadMini] = React.useState<boolean>(true);

  const getListOptions = async () => {
    const localOptions = localStorage.getItem('options');
    if (localOptions) {
      return setOptions(JSON.parse(localOptions));
    }

    try {
      const response = await api.post('/screening/notes-option', {
        type: 'document', //"{document/proctoring}"
      });
      const result = response.data.data;
      const normalizeOptions = result.map((item, index) => ({
        value: item.id,
        label: item.notes_name,
      }));
      localStorage.setItem('options', JSON.stringify(normalizeOptions));
      setOptions(normalizeOptions);
    } catch (error) {
      console.error(error);
    }
  };

  React.useEffect(() => {
    if (options.length > 0 && stateValidation?.final_notes_selected) {
      const valueFromStateValidation = JSON.parse(stateValidation.final_notes_selected);
      // console.log('🐙 ~ React.useEffect ~ valueFromStateValidation:', valueFromStateValidation);
      const filteredOptionsForValue = options.filter((option) => valueFromStateValidation.includes(option.value));
      setValueOptions(filteredOptionsForValue);
    }
  }, [options, stateValidation?.final_notes_selected]);

  const getDashboardMini = async () => {
    setIsLoadingDashboadMini(true);
    try {
      const response = await api.post('screening/document/dashboard/mini', {});
      const result = response.data.data;
      setListDashboardMini(result);
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoadingDashboadMini(false);
    }
  };

  const getDataVerDocList = async (payload?) => {
    setIsLoadingDataVerDocList(true);
    try {
      const response = await api.post('/screening/document/list', payload ?? initialValueField);
      const result = response.data;
      setDataVerDocList(result);
      dispatch({ type: 'MERGED_VALUE', payload: result });
    } catch (error) {
      console.log('🐙 ~ getDataVerDocList ~ error:', error);
    } finally {
      setIsLoadingDataVerDocList(false);
    }
  };

  const getScreeningForm = async (applicant_id: string) => {
    // const form_document = localStorage.getItem('form_document');

    // if (form_document) {
    //   const parse_form_document = JSON.parse(form_document);
    //   console.log('🐙 ~ getScreeningForm ~ parse_form_document:', parse_form_document);
    //   setDataFormVerDoc(parse_form_document);
    // }

    setIsLoadingDataFormVerDoc(true);

    try {
      const payload = { applicant_id };
      const response = await api.post('/screening/document/form', payload);
      const result = response.data.data;
      // localStorage.setItem('form_document', JSON.stringify(result));
      setDataFormVerDoc(result);
    } catch (error) {
      console.log('🐙 ~ getScreeningForm ~ error:', error);
    } finally {
      setIsLoadingDataFormVerDoc(false);
    }
  };

  const getScreeningDetailDocument = async (verdoc_id: string) => {
    setActiveVerdocId(verdoc_id);
    // const detail_document = localStorage.getItem('detail_document');

    // if (detail_document) {
    //   const parse_detail_document = JSON.parse(detail_document);
    //   console.log(desctructuredObject(parse_detail_document));
    //   setDetailValueDocument(parse_detail_document);
    //   setFieldsValueDocument(parse_detail_document);
    //   return;
    // }
    try {
      const payload = { verdoc_id };
      const response = await api.post('/screening/document/detail', payload);
      const result = response.data.data.shift();
      setDetailValueDocument(result);
      dispatchValidation({ type: 'MERGED_VALIDATION', payload: result });
    } catch (error) {
      console.log('🐙 ~ getScreeningForm ~ error:', error);
    }
  };

  const resetState = () => {
    dispatchValidation({ type: 'RESET_VALIDATION' });
    setDataFormVerDoc([]);
    setDetailValueDocument([]);
    setDetailApplicant(null);
    setActiveVerdocId('');
  };

  const getDataDetailApplicant = async (applicant_id: string) => {
    setIsLoadingDetailApplicant(true);
    try {
      const response = await api.post('/screening/document/applicant', {
        applicant_id,
      });
      const result = response.data.data;
      setDetailApplicant(result);
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoadingDetailApplicant(false);
    }
  };

  React.useEffect(() => {
    getDashboardMini();
    getListOptions();
  }, []);

  React.useEffect(() => {
    if (userLevel) {
      getDataVerDocList();
    } else {
      const payload = {
        ...state,
        screener_user_id: '',
        category: 'qc',
      };
      getDataVerDocList(payload);
    }
  }, [userLevel]);

  const handleChange = (e) => {
    const { value } = e.target;
    dispatch({ type: 'CHANGE_SEARCH', payload: { search: value } });
  };

  const handleSearch = async () => {
    await getDataVerDocList(state);
  };

  const handleClickClearValueSearch = async () => {
    const payload = {
      ...state,
      search: '',
    };

    dispatch({ type: 'CHANGE_SEARCH', payload: { search: '' } });
    await getDataVerDocList(payload);
  };

  return (
    <main className="-my-5 h-[89vh] flex flex-col">
      {isLoadingDashboardMini ? <p>Loading</p> : <Header listDashboardMini={listDashboardMini} getDataVerDocList={getDataVerDocList} />}

      <div className="flex-1 flex flex-row overflow-y-hidden mt-2 space-x-2">
        <div className="w-1/4 overflow-y-scroll bg-white p-2 rounded-t-lg border">
          <div className="space-y-2">
            <div className=" flex items-center space-x-2">
              <div className="relative">
                <input
                  value={state.search}
                  placeholder="Input email disini"
                  onChange={handleChange}
                  name="search"
                  type="text"
                  className="w-full px-7 py-1.5 text-sm border border-gray-200 rounded"
                />
                <RxMagnifyingGlass className="size-4 absolute top-2 left-2 text-blue-gray-500" />
                {state.search !== '' && (
                  <button onClick={handleClickClearValueSearch} className="absolute top-1.5 right-2 text-red-500 p-0.5 rounded-md hover:bg-gray-100">
                    <RxCross2 className="size-4" />
                  </button>
                )}
              </div>
              <button onClick={handleSearch} className="px-4 py-1.5 text-xs bg-blue-100 text-blue-700 hover:bg-blue-50 rounded">
                Search
              </button>
            </div>
            <hr />
            <ScreeningTabs
              dataTable={dataVerDocList}
              getScreeningForm={getScreeningForm}
              getScreeningDetailDocument={getScreeningDetailDocument}
              getDataDetailApplicant={getDataDetailApplicant}
              resetState={resetState}
              activeVerdocId={activeVerdocId}
              isLoadingDataTable={isLoadingDataVerDocList}
              setIsLoadingDataTable={setIsLoadingDataVerDocList}
              getDataVerDocList={getDataVerDocList}
            />
          </div>
        </div>

        {activeVerdocId ? (
          <div className="flex flex-col flex-1">
            <div className="flex overflow-y-hidden space-x-2 h-full">
              {isLoadingDataFormVerDoc ? (
                <p>loading</p>
              ) : (
                <main className="w-1/3 overflow-y-scroll bg-white rounded-t-lg p-2 space-y-2">
                  {dataFormVerDoc.map((validationData, index) => {
                    return (
                      <ValidationCard key={validationData.id} validationData={validationData} setOptions={setOptions}>
                        {validationData.list_fields.map((validationValue, index) => {
                          return (
                            <ValidationComponentValue key={validationValue.id} validationData={validationData} validationValue={validationValue} />
                          );
                        })}
                      </ValidationCard>
                    );
                  })}
                  <ValidationCard id="last" validationData={{ doc_name: 'akhir' }}>
                    <LastValidationComponentValue
                      valueOptions={valueOptions}
                      getDataVerDocList={getDataVerDocList}
                      getDashboardMini={getDashboardMini}
                    />
                  </ValidationCard>
                </main>
              )}

              {detailApplicant && <DetailApplicant detailApplicant={detailApplicant} />}
            </div>
            <FooterScreeningDocument getDataVerDocList={getDataVerDocList} getDashboardMini={getDashboardMini} resetState={resetState} />
          </div>
        ) : (
          <MemoizeNullComponent text="render child" />
        )}
      </div>
    </main>
  );
}

function NullComponent({ text }) {
  return (
    <div className="h-full flex-1 bg-white rounded flex flex-col items-center justify-center">
      <GoInbox className="w-20 h-20" />
      <p className="text-center">Belum ada pelamar yang dipilih</p>
    </div>
  );
}

const MemoizeNullComponent = React.memo(NullComponent);
